	import uniSwiperDot from "../uni-swiper-dot/uni-swiper-dot.vue";
	import priceSelect from '../price-select/sl-filter.vue';
	import Messages from '../../util/messages.js';
	import Constant from '../../util/constant.js';
	export default {
		data() {
			return {
				defaultImg: Constant.defaultImg, //默认图片
				flag:true, //改变背景颜色
				current: 0, //轮播图下标
				mode: 'nav', //轮播图类型
				list: [], // 结果列表
				themeColor:'rgba(255,136,0,0.1)',
				designIpt:1, //判断是报价还是结果页面
				// 下拉框列表 
				menuList: [{
						'title': '',
						'detailTitle': '装修风格',
						'key': 'style',
						'isMutiple': false,
						'detailList': []
					},		
					{
						'title': '',
						'key': 'decorateType',
						'isMutiple': false,
						'detailTitle': '装修类型',
						'detailList': []
					},
					{
						'title': '',
						'key': 'area',
						'isSort':true,
						'detailTitle': '房屋面积',
						'detailList': []
					},
					{
						'title': '',
						'key': 'layout',
						'isMutiple': false,
						'detailTitle': '装修户型',
						'detailList': []
					}
				],
				// 请求参数
				params:{
					style:'',
					decorateType:'',
					houseArea:'',
					customerHouseType:'',
					pageNo: Constant.page.pageNo,
					pageSize: Constant.page.pageSize,
				},
				totalPage:'' //总页数
			};
		},
		components: {
			uniSwiperDot,
			priceSelect
		},
		/* props:{
			// 上拉下拉请求到的数据
			addList:{
				type:Array
			}
		},
		computed:{
			getAddList(){
				return this.addList
			}
		}, 
		watch:{
			// 上拉下拉刷新数据
			getAddList(){
				this.list = this.list.concat(this.addList)
			}
		},*/
		methods: {
			// 轮播图总的数量
			change(e) {
				this.current = e.detail.current;
			},
			// 下拉框结果
			result(val) {
				console.log(val)
				if(val == undefined){
					
				}else{
					this.params.style = val.style;
					this.params.decorateType = val.decorateType;
					if(val.area.indexOf('m²') == -1){
						let designList = uni.getStorageSync('designList');
						this.params.houseArea = designList[2].designArea;
					}else{
						this.params.houseArea = val.area.slice(0,val.area.indexOf('m²'));
					}
					this.params.customerHouseType = val.layout;
					this.getList(true);
					this.$emit('params',this.params)
				}
			},
			// 修改背景
			receptFlag(e){
				this.flag = e;
			},
			// 请求结果
			getList(...args){
				uni.showLoading();
				this.$http.selectDesignResultList(this.params).then(res=>{
					uni.hideLoading();
					if (args && args[0]) {
						this.params["pageNo"] = Constant.page.pageNo;
						this.list = [];
					}
					this.params["pageSize"] = Constant.page.pageSize;
					if(res && res.code == 200){
						console.log(res)
						this.totalPage = res.data.pageCount;
						this.list = this.list.concat(res.data.pageSet);
					}else{
						uni.showToast({
							icon: "none",
							title: res.msg || Messages.FAIL_INFO
						})
					}
				})
			},
			//跳转到详情页面
			toDetail(quoteId){
				this.$openPage({
					name: 'example',
					query: {
						quoteId
					}
				})
			},
			// 跳转out外网桥梁页面
			handleGoOut(url){
			  this.$openPage({ name: 'out',query: { url }})
			},
			//下拉加载更多
			loadMore(){
				if(this.totalPage <= this.params["pageNo"]){
					uni.showToast({
						icon: 'none',
						title: Messages.LOADING_FINISH,
					});
				}else{
					this.params["pageNo"] ++;
					this.changeData();
				}
			}	
		},
		mounted(){
			// 下拉框里面的内容
			let list = uni.getStorageSync('catalogues');
			let layoutList = uni.getStorageSync('layoutList');
			let style= list.categorys.filter((item)=>{
				return item.type == 2;
			})
			let styleList = style.map(item=>{return{'title':item.decorateStyle, 'value':item.decorateStyle}});
			styleList.unshift({'title':'全部', 'value':'全部'})
			this.menuList[0].detailList = styleList;
			let decType= list.categorys.filter((item)=>{
				return item.type == 1;
			})
			let decTypeList = decType.map(item=>{return{'title':item.decorateStyle, 'value':item.decorateStyle}});
			decTypeList.unshift({'title':'全部', 'value':'全部'})
			decTypeList.pop();
			this.menuList[1].detailList = decTypeList;
			let houseTypes = layoutList.map(item=>{return{'title':item, 'value':item}});
			houseTypes.unshift({'title':'全部', 'value':'全部'})
			this.menuList[3].detailList = houseTypes;
			let designList = uni.getStorageSync('designList');
			let styleIndex = styleList.findIndex(item=>{
				return item.title == designList[0].designManner
			})
			this.menuList[0].defaultSelectedIndex = styleIndex;
			let decIndex = decTypeList.findIndex(item=>{
				return item.title == designList[1].designDectype
			})
			this.menuList[1].defaultSelectedIndex = decIndex;
			let houseIndex = houseTypes.findIndex(item=>{
				return item.title == designList[3].designLayout
			})
			if(houseIndex !== -1){
				this.menuList[3].defaultSelectedIndex = houseIndex;
			}
			this.$refs.slFilter.resetMenuList(this.menuList);
		},
		created(){
			// 改变下拉框头部标题
			let designList = uni.getStorageSync('designList');
			this.menuList[0].title = designList[0].designManner;
			this.menuList[1].title = designList[1].designDectype;
			this.menuList[2].title = designList[2].designArea + 'm²';
			if(designList[3].designLayout == ''){
				this.menuList[3].title = '户型'
			}else{
				this.menuList[3].title = designList[3].designLayout;
			}
			this.params.style = designList[0].designManner;
			this.params.decorateType = designList[1].designDectype;
			this.params.houseArea = designList[2].designArea;
			this.params.customerHouseType = designList[3].designLayout;
			this.getList(true);
			this.$emit('params',this.params)
			this.$refs.slFilter.resetMenuList(this.menuList);
		}
    }
